.login-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #CADCFC;
}

.welcome-message {
    font-size: 2rem;
    text-align: center;
    margin-bottom: 2rem;
}

.login-button,
.login-button-1 {
    background-color: #00246B;
    color: #fff;
    font-size: 1.2rem;
    border: none;
    border-radius: 0.3rem;
    padding: 1rem 2rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

input[type=number] {
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    font-size: 16px;
    margin-bottom: 10px;
}

.connected-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #CADCFC;
}

.candidates-table {
    border-collapse: collapse;
    width: 50%;
    text-align: center;
}

.candidates-table th,
.candidates-table td {
    padding: 8px;
    text-align: left;
    border-bottom: 1px solid #ddd;
}

.candidates-table th {
    background-color: #f2f2f2;
}